import { useUser } from "@clerk/clerk-react"
import { HeaderSearchBar } from "@rp/react/web/common/layout"
import { Box, Typography } from "@rp/react/web/mui/core"
import { WatchlistsCardViewTabs, WatchlistsTab } from "@rp/react/web/watchlists"
import { DEFAULT_ORGANIZATION_NAME } from "../constants"

type BriefWatchlistsSubheaderProps = {
  shouldHideFilter: boolean
  activeTab: WatchlistsTab
  onTabChange: (tab: WatchlistsTab) => void
  canShare: boolean
  filterTerm: string
  setFilterTerm: (term: string) => void
  filteredResultsCount: number
  isSearching: boolean
  hasNoResults: boolean
}

function BriefWatchlistsSubheader({
  shouldHideFilter,
  activeTab,
  onTabChange,
  canShare,
  filterTerm,
  setFilterTerm,
  filteredResultsCount,
  isSearching,
  hasNoResults,
}: BriefWatchlistsSubheaderProps) {
  const { user } = useUser()
  const organizationName =
    user?.organizationMemberships?.[0]?.organization?.name || DEFAULT_ORGANIZATION_NAME

  const handleTabChange = (newValue: WatchlistsTab) => {
    if (newValue) {
      onTabChange(newValue)
    }
  }

  return (
    <Box data-testid="brief-watch-subheader">
      {/* Search Section */}
      {!shouldHideFilter && (
        <Box sx={{ mb: 2, display: "flex", width: "100%" }}>
          <HeaderSearchBar
            filterTerm={filterTerm}
            onChange={setFilterTerm}
            maxWidth="100%"
            key="brief-select-wl-header-search-bar"
            placeholder="Find in Watchlists"
          />
        </Box>
      )}

      {/* Search Results Count - Show when searching */}
      {isSearching && !hasNoResults && (
        <Typography variant="body2" color="text.secondary" sx={{ ml: 1, mt: 3, minHeight: 32 }}>
          {filteredResultsCount} watchlist{filteredResultsCount !== 1 ? "s" : ""} found:
        </Typography>
      )}

      {/* Tabs Section - Hide when searching */}
      {!isSearching && (
        <Box sx={{ mb: 2 }}>
          <WatchlistsCardViewTabs
            canShare={canShare}
            tabValue={activeTab}
            handleChange={handleTabChange}
            organizationName={organizationName}
          />
        </Box>
      )}
    </Box>
  )
}

export { BriefWatchlistsSubheader }
